<template>
  <!-- :style="{ zIndex: isShow ? '23' : '' }" -->
  <transition name="el-zoom-in-bottom">
    <div class="playView" v-if="isShow">
      <!-- 背景模糊部分 -->
      <div
        class="bg"
        :style="{ backgroundImage: `url(${playDetail.al.picUrl})` }"
      ></div>
      <!-- 顶部返回区域 -->
      <div class="playTop">
        <span class="iconfont icon-zuo" @click="setIsShow"></span>
        <div class="center">
          <p class="title">{{ playDetail.name }}</p>
          <p class="author">
            <span @click="toSingerView(playDetail, 0)">{{
              playDetail.ar[0].name
            }}</span>
            <span @click="toSingerView(playDetail, 1)" v-if="playDetail.ar[1]"
              >/{{ playDetail.ar[1].name }}</span
            >
            <span @click="toSingerView(playDetail, 2)" v-if="playDetail.ar[2]"
              >/{{ playDetail.ar[2].name }}</span
            >
            <span @click="toSingerView(playDetail, 3)" v-if="playDetail.ar[3]"
              >/{{ playDetail.ar[3].name }}</span
            >
          </p>
        </div>
        <span class="iconfont icon-fenxiang"></span>
      </div>
      <!-- 点击出现歌词部分 -->
      <transition name="el-zoom-in-top">
        <div
          class="playLyric"
          v-show="isShowLyric"
          ref="playLyric"
          @click="setShowLyric()"
        >
          <span class="notLyric" v-if="lyric === ''">纯音乐，请欣赏~</span>
          <p
            :class="{
              active:
                currentTime * 1000 >= item.pre1 &&
                currentTime * 1000 <= item.pre2,
            }"
            v-for="(item, index) in $store.getters.lyricList"
            :key="index"
            v-else
          >
            {{ item.lyric }}
          </p>
        </div>
      </transition>
      <!-- 底部上层小图标 -->
      <transition name="el-fade-in-linear">
        <div class="gradientBottom" v-if="!isShowLyric">
          <span
            :class="
              isLikeSong ? 'iconfont icon-shixin' : 'iconfont icon-kongxin'
            "
            @click="reqLikeSong"
          ></span>
          <span class="iconfont icon-comment" @click="showComment"></span>
          <span class="iconfont icon-gengduo"></span>
        </div>
      </transition>
      <!-- 进度条部分 -->
      <transition name="el-fade-in-linear">
        <div class="progress" v-if="!isShowLyric">
          <span>{{ curTime }}</span>
          <!-- 总进度条 -->
          <div class="playBar">
            <!-- 实时进度条 -->
            <div class="timePlayBar" :style="{ width: currentWidth + '%' }">
              <!-- 小圆球 -->
              <div class="circle"></div>
            </div>
          </div>
          <span>{{ duration }}</span>
        </div>
      </transition>
      <!-- 底部下层小图标 -->
      <div class="playFooter">
        <span class="iconfont icon-xunhuan-wangyiicon"></span>
        <span
          @click="onMusic(), handleGetUrl(), hiddenLyric()"
          class="iconfont icon-shangyishou"
        ></span>
        <span
          :class="
            isPause
              ? 'iconfont icon-bofang101'
              : 'iconfont icon-a-zantingtingzhi'
          "
          @click="audioPlay"
        ></span>
        <span
          @click="nextMusic(), handleGetUrl(), hiddenLyric()"
          class="iconfont icon-shangyishou1"
        ></span>
        <span class="iconfont icon-gedan"></span>
      </div>
    </div>
  </transition>
  <!-- 转盘部分 -->
  <div
    class="playContent"
    :style="{ left: !isShowLyric ? '' : '10rem', zIndex: isShow ? '' : '-10' }"
    @click="setShowLyric()"
  >
    <img
      :class="isPause ? '' : 'rotate'"
      src="@/assets/images/player-bar.png"
      alt
    />
    <div class="turnTable">
      <img src="@/assets/images/player.png" alt />
      <img
        :class="
          isPause
            ? isAnimation
              ? 'coverAnimation cover suspended'
              : 'coverAnimation1 cover suspended'
            : isAnimation
            ? 'coverAnimation cover '
            : 'coverAnimation1 cover'
        "
        :src="playDetail.al.picUrl"
        alt
      />
    </div>
  </div>
  <!-- 评论模块 -->
  <transition name="el-zoom-in-bottom">
    <comment
      v-if="isShowComment"
      :isShowComment="isShowComment"
      :playDetail="playDetail"
    />
  </transition>
</template>
<script>
import comment from '@/components/common/comment'
import { getIsLike } from '@/tools/request'

import { mapMutations, mapState } from 'vuex'
import moment from 'moment'
export default {
  components: {
    comment,
  },
  emits: ['isUp', 'back'],
  props: [
    'playDetail',
    'isPause',
    'audioPlay',
    'isShow',
    'isShowLyric',
    'duration',
    'durSec',
    'isAnimation',
  ],
  data() {
    return {
      // 进度条 左边实时时间
      curTime: '00:00',
      // 实时进度条进度
      currentWidth: 0,
      // 实时小播放器上下移动
      isUp: false,
      // 显示评论
      isShowComment: false,
      // 图片正在加载
      // isLoadingPic: false,
    }
  },
  updated() {},
  methods: {
    ...mapMutations([
      'nextMusic',
      'onMusic',
      'setIsShow',
      'setShowLyric',
      'hiddenLyric',
    ]),
    // 一位数 前面补0 回调
    num(num) {
      if (num < 10) {
        return '0' + num
      } else {
        return num
      }
    },
    // 实时时间回调
    curTimeCallBack() {
      const duration = moment.duration(this.$store.state.currentTime, 'seconds')
      const format =
        '0' +
        Math.floor(duration.asMinutes()) +
        ':' +
        this.num(duration.seconds())
      // console.log(this.$store.state.currentTime)
      this.curTime = format
    },
    // 进度条
    currentWidthCallBack() {
      let currentWidthData = (this.currentTime / this.durSec) * 100
      this.currentWidth = currentWidthData.toFixed(0)
    },
    // 显示评论
    showComment() {
      this.isShowComment = !this.isShowComment
    },
    // 点击获取播放歌曲url
    handleGetUrl() {
      if (this.playCurrentIndex !== Number) {
        this.$store.dispatch('reqLyric', this.nowPlay.id)
      }
      this.$store.dispatch('reqSongUrl', this.nowPlay.id)
    },
    // 点击喜欢/取消喜欢音乐
    async reqLikeSong() {
      if (localStorage.getItem('user') == null) {
        this.$toast('请先登录哦!')
      } else {
        if (!this.isLikeSong) {
          let result = await getIsLike(this.nowPlay.id, true)
          if (result.data.code == 200) {
            this.$toast('喜欢成功')
            this.$store.commit('setUserLikeSong', true)
          }
        } else {
          let result = await getIsLike(this.nowPlay.id, false)
          if (result.data.code == 200) {
            this.$toast('取消喜欢成功')
            this.$store.commit('setUserLikeSong', false)
          }
        }
      }
    },
    // 点击去到歌手页面
    toSingerView(value, index) {
      this.$store.commit('clearSingerDetailList')
      if (value.ar[index].id == 0) {
        this.$store.commit('setIsShow')
      } else {
        this.$store.dispatch('reqArtist', value.ar[index].id)
        this.$store.dispatch('reqArtistSong', value.ar[index].id)
        this.$router.push('/singer')
        this.$store.commit('setIsShow')
      }
    },
  },
  computed: {
    ...mapState(['currentTime', 'lyric', 'isLikeSong', 'nowPlay']),
  },
  watch: {
    currentTime() {
      clearTimeout(timer)
      let timer = setTimeout(() => {
        if (this.lyric !== '' && this.isShow) {
          let p = document.querySelector('p.active')
          if (p) {
            let offsetTop = p.offsetTop
            this.$refs.playLyric.scrollTop = offsetTop - 250
          }
        } else {
          clearTimeout(timer)
        }
      }, 0)
      if (this.$route.path == '/' || this.$route.path == '/personal') {
        this.isUp = true
        this.$emit('isUp', this.isUp)
      } else {
        this.isUp = false
        this.$emit('isUp', this.isUp)
      }
      this.curTimeCallBack()
      this.currentWidthCallBack()
    },
  },
}
</script>
<style lang="less" scoped>
.icon-shixin {
  color: #d43c33 !important;
}
.playContent {
  position: fixed;
  top: 1rem;
  left: 1.67rem;
  display: flex;
  flex-direction: column;
  align-items: center;
  z-index: 24;
  overflow-y: visible;
  > img {
    width: 2.4rem;
    margin-left: 1.7rem;
    z-index: 1;
    transform-origin: 0.3rem 0;
    transition: all 0.5s;
    transform: rotate(-15deg);
  }
  .rotate {
    transform: rotate(8deg) !important;
  }
  .turnTable {
    position: absolute;
    top: 2.2rem;
    width: 6rem;
    background-color: rgba(168, 168, 168, 0.288);
    border-radius: 50%;
    margin: 0 !important;
    img {
      position: relative;
      top: 2px;
      width: 100%;
      padding: 0.1rem;
    }
    .cover {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      width: 65%;
      height: 65%;
      border-radius: 50%;
    }
    .coverAnimation {
      animation: cover 18s linear infinite;
      animation-delay: 0.5s;
    }
    .coverAnimation1 {
      animation: cover1 18s linear infinite;
      animation-delay: 0.5s;
    }
    .suspended {
      animation-play-state: paused;
      -webkit-animation-play-state: paused; /* Safari 和 Chrome */
    }
    @keyframes cover {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }
    @keyframes cover1 {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }
  }
}
.playView {
  position: fixed;
  top: 0;
  left: 0;
  width: 100vw;
  height: 100vh;
  background-color: rgb(0, 0, 0);
  z-index: 23;
  .bg {
    position: absolute;
    top: 0;
    left: 0;
    width: 100vw;
    height: 100vh;
    background: no-repeat top fixed;
    background-size: 150% 150%;
    filter: blur(20px) brightness(70%);
    transform: scale(2);
    z-index: -10;
  }
  .playTop {
    display: flex;
    width: 7.5rem;
    height: 1.1rem;
    padding: 0.3rem;
    align-items: center;
    justify-content: space-between;
    color: #fff;
    background: -webkit-linear-gradient(top, #7373733d 0%, #6b020200 100%);
    .iconfont {
      font-size: 0.4rem;
    }
    .icon-fenxiang {
      font-size: 0.45rem;
    }
    .center {
      width: 5rem;
      text-align: center;
      .title,
      .author {
        white-space: normal;
        overflow: hidden;
        display: -webkit-box;
        -webkit-line-clamp: 1;
        -webkit-box-orient: vertical;
      }
      .author {
        font-size: 0.25rem;
        color: #f5f5f5b4;
      }
    }
  }
  .gradientBottom {
    width: 7.5rem;
    height: 1rem;
    position: absolute;
    bottom: 2.7rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    span {
      color: #e3e3e3;
      font-size: 0.4rem;
    }
  }
  .progress {
    position: absolute;
    bottom: 2rem;
    left: 5.5%;
    width: 90%;
    display: flex;
    align-items: center;
    justify-content: center;
    font-size: 10px;
    color: #ffffffab;
    .playBar {
      position: relative;
      flex: 1;
      margin: 0 10px;
      height: 2px;
      background-color: #ffffff42;
      .timePlayBar {
        position: absolute;
        top: 0;
        left: 0;
        max-width: 99% !important;
        height: 2px;
        z-index: 1;
        background-color: #ffffff9d;
        .circle {
          position: absolute;
          right: -2px;
          top: -4px;
          width: 9px;
          height: 9px;
          border-radius: 50%;
          background-color: #fff;
          box-shadow: 0px 0px 20px 3px rgba(0, 0, 0, 0.2);
        }
      }
    }
  }
  .playFooter {
    width: 7.5rem;
    height: 1rem;
    position: absolute;
    bottom: 0.5rem;
    display: flex;
    align-items: center;
    justify-content: space-around;
    .iconfont {
      color: #eee;
      font-size: 0.4rem;
    }
    .icon-bofang101,
    .icon-a-zantingtingzhi {
      border: 1px solid #eee;
      padding: 0.3rem;
      border-radius: 50%;
    }
  }
  .playLyric {
    position: absolute;
    width: 7.5rem;
    height: 10.8rem;
    top: calc(50% - 5.8rem);
    left: 0;
    overflow: scroll;
    text-align: center;
    p:first-child {
      margin-top: 3rem;
    }
    p:last-child {
      margin-bottom: 3rem;
    }
    p {
      font-size: 0.3rem;
      padding: 0.2rem 0;
      margin: 0 0.2rem;
      transition: all 0.3s;
      color: #dadadacb;
    }
    .active {
      padding: 0.2rem 0 !important;
      color: rgb(238, 238, 238);
      transition: all 0.3s;
      font-size: 0.31rem;
    }
  }
  .notLyric {
    position: relative;
    top: 50%;
    left: 0;
    right: 0;
    bottom: 0;
    color: #fff;
  }
}
</style>
