import { createStore } from 'vuex'
import {
  getLyric,
  Login,
  getUserDetail,
  getSongUrl,
  getArtist,
  getArtistSong,
  refreshLogin,
} from '@/tools/request'

export default createStore({
  state: {
    // 渲染当前进入歌单数组
    showList: [{ al: {}, ar: [{ name: '' }] }],
    // 当前播放歌单数组
    playList: [{ al: {}, ar: [{ name: '' }] }],
    // 当前播放歌曲数组
    nowPlay: {},
    // 当前播放歌曲的标记
    nowPlayList: null,
    // 当前播放列表中 选中的下标
    playCurrentIndex: Number,
    // 播放歌曲是否暂停
    isPause: true,
    // 播放器动画
    isAnimation: false,
    // 是否显示播放页面
    isShow: false,
    // 是否显示歌词
    isShowLyric: false,
    // 当前播放音乐歌词
    lyric: '',
    // 当前播放音乐总时长
    currentTime: 0,
    intervalId: 0,
    // 当前播放音乐url
    songUrl: '',
    // 用户喜欢的歌曲所有id数组
    userLikeSongId: [],
    // 是否喜欢该歌曲
    isLikeSong: false,
    // 歌手详情数组
    singerDetailList: [],
    // 重复点击一首歌 不发送请求
    songNotReq: 0,
  },

  getters: {
    lyricList(state) {
      let arr = state.lyric.split(/\n/gis).map((item, i) => {
        let min = parseInt(item.slice(1, 3))
        let sec = parseInt(item.slice(4, 6))
        let mill = parseInt(item.slice(7, 10))
        let lyric_arr = item.split(']')
        let lyric = lyric_arr.pop()

        return {
          min,
          sec,
          mill,
          lyric,
          content: item,
          time: mill + sec * 1000 + min * 60 * 1000,
        }
      })
      arr.forEach((item, i) => {
        if (i != 0 && arr[i].time) {
          item.pre1 = arr[i].time
          item.pre2 = arr[i + 1].time
        }
      })
      return arr
    },
  },
  mutations: {
    // 音乐播放状态
    setMusicPlayTrue(state) {
      state.isPause = true
    },
    setMusicPlayFalse(state) {
      state.isPause = false
    },
    // 获取音乐列表
    setPlaylist(state, value) {
      state.showList = value
    },
    // 全部播放
    setAllPlay(state, value = 0) {
      state.playList = state.showList
      state.playCurrentIndex = value
      state.nowPlay = state.playList[state.playCurrentIndex]
      state.isShow = true
      state.isShowLyric = false
      if (localStorage.getItem('userLikeSongId') !== null) {
        let result = localStorage.getItem('userLikeSongId').split(',')
        for (let i = 0; i < result.length - 1; i++) {
          if (result[i] == state.nowPlay.id) {
            return (state.isLikeSong = true)
          } else {
            state.isLikeSong = false
          }
        }
      }
    },
    // 点击获取下标 存入当前播放歌曲到全局状态
    setPlayIndex(state, value) {
      state.playList = state.showList
      state.playCurrentIndex = value.index
      if (state.nowPlay.id && state.nowPlay.id !== value.item.id) {
        if (state.nowPlayList !== value.index) {
          state.isAnimation = !state.isAnimation
          state.isPause = true
        }
        state.nowPlayList = value.index
      }
      state.nowPlay = state.playList[state.playCurrentIndex]
      // console.log(state.nowPlay)
      state.isShow = true
      state.isShowLyric = false
      if (localStorage.getItem('userLikeSongId') !== null) {
        let result = localStorage.getItem('userLikeSongId').split(',')
        for (let i = 0; i < result.length - 1; i++) {
          if (result[i] == state.nowPlay.id) {
            return (state.isLikeSong = true)
          } else {
            state.isLikeSong = false
          }
        }
      }
    },
    // 当前列表中 播放下一首的回调
    nextMusic(state, value = 1) {
      if (
        state.playList.slice(-1)[0].id !==
        state.playList[state.playCurrentIndex].id
      ) {
        state.playCurrentIndex += value
        state.nowPlay = state.playList[state.playCurrentIndex]
        state.songUrl = ''
        state.isPause = true
        state.isShowLyric = false
        state.isAnimation = !state.isAnimation
        state.nowPlayList = state.playCurrentIndex
        if (localStorage.getItem('userLikeSongId') !== null) {
          let result = localStorage.getItem('userLikeSongId').split(',')
          for (let i = 0; i < result.length - 1; i++) {
            if (result[i] == state.nowPlay.id) {
              return (state.isLikeSong = true)
            } else {
              state.isLikeSong = false
            }
          }
        }
      } else {
        state.playCurrentIndex = 1
        state.nowPlay = state.playList[0]
        state.songUrl = ''
        state.isPause = true
        state.isShowLyric = false
        state.isAnimation = !state.isAnimation
        state.nowPlayList = state.playCurrentIndex
        if (localStorage.getItem('userLikeSongId') !== null) {
          let result = localStorage.getItem('userLikeSongId').split(',')
          for (let i = 0; i < result.length - 1; i++) {
            if (result[i] == state.nowPlay.id) {
              return (state.isLikeSong = true)
            } else {
              state.isLikeSong = false
            }
          }
        }
      }
    },
    // 当前列表中 播放上一首的回调
    onMusic(state, value = 1) {
      if (state.playCurrentIndex !== 0) {
        state.playCurrentIndex -= value
        state.nowPlay = state.playList[state.playCurrentIndex]
        state.songUrl = ''
        state.isPause = true
        state.isShowLyric = false
        state.isAnimation = !state.isAnimation
        state.nowPlayList = state.playCurrentIndex
        if (localStorage.getItem('userLikeSongId') !== null) {
          let result = localStorage.getItem('userLikeSongId').split(',')
          for (let i = 0; i < result.length - 1; i++) {
            if (result[i] == state.nowPlay.id) {
              return (state.isLikeSong = true)
            } else {
              state.isLikeSong = false
            }
          }
        }
      }
    },
    // 获取歌词
    setLyric(state, value) {
      state.lyric = value
    },
    // 更新歌曲时间
    setCurrentTime(state, value) {
      state.currentTime = value
    },
    // 进入/退出 playView 页面
    setIsShow(state) {
      state.isShow = !state.isShow
    },
    // 退出登录调用
    setUserDetail(state) {
      state.userDetail = []
    },
    // 修改songUrl
    setSongUrl(state, value) {
      state.songUrl = value
    },
    // 点击切换歌词
    setShowLyric(state) {
      state.isShowLyric = !state.isShowLyric
      // console.log(state.isShowLyric)
    },
    // 取消显示歌词
    hiddenLyric(state) {
      state.isShowLyric = false
    },
    // 修改是否喜欢歌曲
    setUserLikeSong(state, value) {
      state.isLikeSong = value
    },
    // 修改用户喜欢歌曲id
    setUserLikeSongId(state, value) {
      state.userLikeSongId = value
    },
    // 进入歌手页面之前 先清空之前的数据
    clearSingerDetailList(state) {
      state.singerDetailList = []
    },
  },
  actions: {
    async reqLyric(content, payload) {
      if (content.state.songNotReq !== content.state.nowPlay.id) {
        content.commit('setLyric', '')
        let result = await getLyric(payload)
        if (result.data.lrc !== undefined) {
          content.commit('setLyric', result.data.lrc.lyric)
        }
      }
    },
    async Login(content, payload) {
      let result = await Login(payload.phone, payload.password)
      if (result.data.code == 200) {
        // let refresh = await refreshLogin()
        // console.log(refresh)
        let user = await getUserDetail(result.data.account.id)
        if (user.data.code == 200) {
          // content.state.userDetail = user.data
          localStorage.setItem('user', JSON.stringify(user.data))
        }
      }
      return result
    },
    // 封装获取音乐url方法
    async reqSongUrl({ dispatch, commit, state }, payload) {
      if (state.songNotReq !== state.nowPlay.id) {
        if (state.nowPlay.id) {
          // console.log(state.nowPlay.id)
          // console.log(payload)
          let result = await getSongUrl(state.nowPlay.id)
          console.log('reqSongUrl')
          if (result.data.data[0].url !== null) {
            commit('setSongUrl', result.data.data[0].url)
            state.songNotReq = payload
          } else {
            commit('nextMusic')
            commit('setSongUrl', '')
            dispatch('reqSongUrl')
          }
        }
      }
    },
    // 封装获取请求歌手详情
    async reqArtist({ dispatch, commit, state }, payload) {
      let id = payload
      let result = await getArtist(id)
      if (result.data.data.user === undefined) {
        let And = result.data.data.artist
        // 背景图片
        state.singerDetailList.bg = And.cover
        // 名字
        state.singerDetailList.nm = And.name
      } else {
        let result1 = await getUserDetail(result.data.data.user.userId)
        let And = result1.data
        // 背景图片
        state.singerDetailList.bg = And.profile.backgroundUrl
        // 头像
        state.singerDetailList.hd = And.profile.avatarUrl
        // 名字
        state.singerDetailList.nm = And.profile.artistName
        // 小图标
        state.singerDetailList.icon = And.profile.avatarDetail.identityIconUrl
        // 关注
        state.singerDetailList.fo = And.profile.follows
        // 粉丝
        state.singerDetailList.fd = And.profile.followeds
        // 等级
        state.singerDetailList.lv = result1.data.level
        // 描述
        state.singerDetailList.cn = And.identify.imageDesc
        // 是否关注
        state.singerDetailList.is = And.profile.followed
      }
      // console.log(state.singerDetailList)
    },
    // 请求歌手所有歌曲
    async reqArtistSong({ dispatch, commit, state }, payload) {
      let id = payload
      let result = await getArtistSong(id)
      commit('setPlaylist', result.data.songs)
    },
  },
  modules: {},
})
