<template>
  <div class="position">
    <div
      class="playController"
      v-if="nowPlay.al !== undefined"
      :style="{ bottom: isTop ? '48px' : '-0.1rem' }"
    >
      <div class="img" @click="setIsShow">
        <img
          :class="
            isPause
              ? isAnimation
                ? 'coverAnimation cover suspended'
                : 'coverAnimation1 cover suspended'
              : isAnimation
              ? 'coverAnimation cover '
              : 'coverAnimation1 cover'
          "
          :src="nowPlay.al.picUrl"
          alt
        />
      </div>
      <div class="left" @click="setIsShow">
        <p>{{ nowPlay.name }}</p>
        <i>&ensp;-&ensp;</i>
        <span>
          {{ nowPlay.ar[0].name }}
          <span v-if="nowPlay.ar[1]">/{{ nowPlay.ar[1].name }}</span>
          <span v-if="nowPlay.ar[2]">/{{ nowPlay.ar[2].name }}</span>
          <span v-if="nowPlay.ar[3]">/{{ nowPlay.ar[3].name }}</span>
        </span>
      </div>
      <div class="right">
        <div
          :class="
            isPause
              ? 'iconfont icon-bofang101'
              : 'iconfont icon-a-zantingtingzhi'
          "
          @click="audioPlay"
        ></div>
        <div class="iconfont icon-gedan"></div>
      </div>
      <audio
        @durationchange="getDuration"
        @play="onPlay"
        @ended="nextMusic(1), handleGetUrl()"
        ref="audio"
        autoplay="autoplay"
        :src="songUrl"
      ></audio>
    </div>

    <play-view
      :isShowLyric="isShowLyric"
      :isAnimation="isAnimation"
      :audioPlay="audioPlay"
      :isPause="isPause"
      :isShow="isShow"
      :duration="duration"
      :durSec="durSec"
      @isUp="isUp"
      @back="isShow = !isShow"
      v-if="nowPlay.al !== undefined"
      :playDetail="nowPlay"
    />
  </div>
</template>
<script>
import { mapState, mapMutations } from "vuex"

import playView from "@/components/common/playView"

export default {
  data () {
    return {
      // 歌曲总时长 分钟
      duration: 0,
      // 歌曲总时长 秒
      durSec: 0,
      isTop: false,
      // 使请求只请求一次
      isReq: false,
    }
  },
  updated () { },
  components: {
    playView,
  },
  computed: {
    ...mapState([
      "playList",
      "playCurrentIndex",
      "isPause",
      "isShow",
      "isShowLyric",
      "nowPlay",
      "songUrl",
      "isAnimation",
    ]),
  },
  methods: {
    audioPlay () {
      if (this.$refs.audio.paused) {
        this.$refs.audio.play()
        this.setMusicPlayFalse()
        this.updateTime()
      } else {
        this.$refs.audio.pause()
        this.setMusicPlayTrue()
      }
    },
    onPlay () {
      clearInterval(this.$store.state.intervalId)
      this.setMusicPlayFalse()
      this.updateTime()
    },
    updateTime () {
      this.$store.state.intervalId = setInterval(() => {
        this.$store.commit("setCurrentTime", this.$refs.audio.currentTime)
      }, 500)
    },
    // 一位数 前面补0 回调
    num (num) {
      if (num < 10) {
        return "0" + num
      } else {
        return num
      }
    },
    // 获取歌曲总时长
    getDuration () {
      this.durSec = this.$refs.audio.duration
      if (this.durSec > 60) {
        let one = "0" + parseInt(this.durSec / 60)
        let two = this.num(parseInt(this.durSec % 60))
        this.duration = one + ":" + two
      } else {
        this.duration = "00" + ":" + this.durSec.toFixed(0)
      }
    },
    isUp (isUp) {
      this.isTop = isUp
    },
    // 点击获取播放歌曲url
    handleGetUrl () {
      if (this.playCurrentIndex !== Number) {
        this.$store.dispatch("reqLyric", this.nowPlay.id)
      }
      this.$store.dispatch("reqSongUrl", this.nowPlay.id)
    },
    ...mapMutations([
      "nextMusic",
      "setMusicPlayFalse",
      "setMusicPlayTrue",
      "setIsShow",
    ]),
  },
}
</script>
<style lang="less" scoped>
.playController {
  position: fixed;
  bottom: 1rem;
  width: 7.5rem;
  height: 0.9rem;
  display: flex;
  border-top: 1px solid rgb(235, 235, 235);
  background-color: #fdfdfd;
  z-index: 23;
  transition: all 0.5s;
  .img {
    position: absolute;
    top: -0.1rem;
    left: 0.4rem;
    width: 0.9rem;
    height: 0.9rem;
    background-color: #000;
    display: flex;
    justify-content: center;
    align-items: center;
    border-radius: 50%;
    img {
      width: 0.65rem;
      height: 0.65rem;
      border-radius: 50%;
    }
    .cover {
      position: absolute;
      top: 0;
      left: 0;
      right: 0;
      bottom: 0;
      margin: auto;
      border-radius: 50%;
    }
    .coverAnimation {
      animation: cover 18s linear infinite;
      animation-delay: 0.5s;
    }
    .coverAnimation1 {
      animation: cover1 18s linear infinite;
      animation-delay: 0.5s;
    }
    .suspended {
      animation-play-state: paused;
    }
    @keyframes cover {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }
    @keyframes cover1 {
      from {
        transform: rotate(0deg);
      }
      to {
        transform: rotate(360deg);
      }
    }
  }
  .left {
    flex: 6;
    max-width: 4rem;
    height: 0.6rem;
    display: flex;
    align-items: flex-end;
    margin-left: 1.5rem;
    i {
      font-size: 0.1rem;
      color: #a5a5a5;
    }
    p {
      font-size: 0.26rem;
      color: #333;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
    span {
      font-size: 0.1rem;
      color: #777;
      white-space: nowrap;
      overflow: hidden;
      text-overflow: ellipsis;
    }
  }
  .right {
    flex: 4;
    display: flex;
    align-items: center;
    justify-content: flex-end;
    .iconfont {
      font-size: 0.4rem;
      margin-right: 0.3rem;
      color: #555;
    }
    .icon-bofang101,
    .icon-a-zantingtingzhi {
      font-size: 0.1rem;
      margin-right: 0.6rem;
      border: 1px solid #777;
      border-radius: 50%;
      padding: 0.1rem;
    }
  }
}
</style>
