import axios from 'axios'
axios.defaults.withCredentials = true
let http = 'http://cloud-music.pl-fe.cn/'
// let http = ' http://localhost:3000/'

// 获取轮播图的api
export function getBanner(type = 0) {
  return axios.get(http + `banner?type=${type}`)
}

// 获取首页图标列表的图标
export function getBall() {
  return axios.get(http + 'homepage/dragon/ball')
}

// 获取推荐歌单的歌单
export function getPersonalList(limit = 10) {
  return axios.get(http + `personalized?limit=${limit}`)
}

// 获取歌单详情
export function getPlaylistDetail(id) {
  return axios.get(http + `playlist/detail?id=${id}`)
}

// 获取歌曲播放url
export function getSongUrl(id) {
  return axios.get(http + `song/url?id=${id}`)
}

// 获取歌曲的歌词
export function getLyric(id) {
  return axios.get(http + `lyric?id=${id}`)
}

// 搜索歌曲接口
export function searchMusic(keyword) {
  return axios.get(http + `cloudsearch?keywords=${keyword}`)
}

// 搜索歌曲接口
export function searchSinger(keyword) {
  return axios.get(http + `search?keywords=${keyword}&type=100`)
}

// 手机登录接口
export function Login(phone, password) {
  return axios.get(http + `login/cellphone?phone=${phone}&password=${password}`)
}

// 获取个人详情接口
export function getUserDetail(userId) {
  return axios.get(http + `user/detail?uid=${userId}`)
}

// 获取个人歌单
export function getUserMusicList(userId) {
  return axios.get(http + `user/playlist?uid=${userId}&limit=50`)
}

// 获取歌曲评论信息
export function getComments(musicId, sortType = 1) {
  return axios.get(
    http + `comment/new?type=0&id=${musicId}&sortType=${sortType}`
  )
}

// 获取歌曲评论楼层评论信息
export function getCommentFloor(parentCommentId, musicId) {
  return axios.get(
    http +
      `comment/floor?parentCommentId=${parentCommentId}&id=${musicId}&type=0`
  )
}

// 获取每日推荐歌曲
export function getRecommend() {
  return axios.get(http + `recommend/songs`)
}

// 获取排行榜内容
export function getTopList() {
  return axios.get(http + `toplist`)
}

// 获取歌手个人信息
export function getArtist(id) {
  return axios.get(http + `artist/detail?id=${id}`)
}

// 获取歌手个人歌曲
export function getArtistSong(id) {
  return axios.get(http + `artist/top/song?id=${id}`)
}

// 获取个人喜欢的歌曲id
export function getLikedSong(id) {
  return axios.get(http + `likelist?uid=${id}`)
}

// 获取喜欢/取消喜欢音乐
export function getIsLike(id, value) {
  return axios.get(http + `like?id=${id}&like=${value}`)
}

// 退出登录调用
export function Logout() {
  return axios.get(http + `logout`)
}

// 刷新登陆状态
export function refreshLogin() {
  return axios.get(http + `login/refresh`)
}

// 请求歌单所有歌曲
export function allSongIds(ids) {
  return axios.get(http + `song/detail?ids=${ids}`)
}
