import {
  createRouter,
  createWebHistory,
  createWebHashHistory,
} from 'vue-router'

const Home = () => import('@/views/Home')
const ListView = () => import('@/views/ListView')
const Search = () => import('@/views/Search')
const Login = () => import('@/views/Login')
const Personal = () => import('@/views/Personal')
const Recommend = () => import('@/views/Recommend')
const Singer = () => import('@/views/Singer')

const routes = [
  {
    path: '/',
    name: 'Home',
    component: Home,
    meta: {
      scrollTop: true,
    },
  },
  {
    path: '/listview',
    name: 'listview',
    component: ListView,
    meta: {
      top: true,
    },
  },
  {
    path: '/search',
    name: 'search',
    component: Search,
  },
  {
    path: '/login',
    name: 'login',
    component: Login,
  },
  {
    path: '/personal',
    name: 'personal',
    component: Personal,
    meta: {
      scrollTop: true,
    },
  },
  {
    path: '/recommend',
    name: 'recommend',
    component: Recommend,
  },
  {
    path: '/singer',
    name: 'singer',
    component: Singer,
    meta: {
      top: true,
    },
  },
]

const router = createRouter({
  history: createWebHashHistory(process.env.BASE_URL),
  routes,
})

router.afterEach((to, from, next) => {
  if (from.matched.some((m) => m.meta.scrollTop)) {
    setTimeout(() => {
      if (from.query.scrollValue !== 0 && from.query.scrollValue) {
        let num = parseInt(from.query.scrollValue)
        document.body.scrollTop = num
        document.documentElement.scrollTop = num
      }
      // next()
    }, 200)
    setTimeout(() => {
      if (from.query.scrollV !== 0 && from.query.scrollV) {
        let num = parseInt(from.query.scrollV)
        document.body.scrollTop = num
        document.documentElement.scrollTop = num
      }
      // next()
    }, 400)
  }
  if (to.matched.some((m) => m.meta.top)) {
    // console.log(to)
    document.body.scrollTop = 0
    document.documentElement.scrollTop = 0
    // next()
  }
})

export default router
