<template>
  <div class="comment">
    <!-- 顶部返回区域 -->
    <div class="playTop">
      <span class="iconfont icon-zuo" @click="backComment()"></span>
      <div class="center">
        <p>评论({{ comTotal }})</p>
      </div>
      <span class="iconfont icon-fenxiang"></span>
    </div>
    <!-- fixed -->
    <div class="listTitle fixed" v-if="showTop">
      <span>评论区</span>
      <div class="tabBarList">
        <span :class="{ active: show == 0 }" @click="oneClick()">推荐</span>
        <span class="line">|</span>
        <span :class="{ active: show == 1 }" @click="towClick()">最热</span>
        <span class="line">|</span>
        <span :class="{ active: show == 2 }" @click="threeClick()">最新</span>
      </div>
    </div>
    <!-- 歌曲大体区域 -->
    <div class="playController" v-if="nowPlayDetail.al !== undefined">
      <div class="img" @click="setIsShow">
        <img :src="nowPlayDetail.al.picUrl" alt />
      </div>
      <div class="left" @click="setIsShow">
        <p>{{ nowPlayDetail.name }}</p>
        <span>
          {{ nowPlayDetail.ar[0].name }}
          <span
            v-if="nowPlayDetail.ar[1]"
          >/{{ nowPlayDetail.ar[1].name }}</span>
          <span v-if="nowPlayDetail.ar[2]">/{{ nowPlayDetail.ar[2].name }}</span>
          <span v-if="nowPlayDetail.ar[3]">/{{ nowPlayDetail.ar[3].name }}</span>
        </span>
      </div>
    </div>
    <!-- 歌曲评论区域 -->
    <div class="commentList">
      <div class="listTitle" ref="listTitle">
        <span>评论区</span>
        <div class="tabBarList">
          <span :class="{ active: show == 0 }" @click="oneClick()">推荐</span>
          <span class="line">|</span>
          <span :class="{ active: show == 1 }" @click="towClick()">最热</span>
          <span class="line">|</span>
          <span :class="{ active: show == 2 }" @click="threeClick()">最新</span>
        </div>
      </div>
      <div class="listItem" v-for="(item, index) in commentList" :key="index">
        <el-image class="listItemImg" :src="item.user.avatarUrl" alt lazy />
        <div class="content">
          <div class="contentTop">
            <div class="left">
              <span>{{ item.user.nickname }}</span>
              <p>{{ conversionTime(item.time) }}</p>
            </div>
            <div class="right">
              <span class="rightCount">{{ item.likedCount }}</span>
              <span
                :class="
                  item.liked
                    ? 'iconfont icon-shixin liked'
                    : 'iconfont icon-zan '
                "
              ></span>
            </div>
          </div>
          <div class="com">
            <p>{{ item.content }}</p>

            <a
              @click="gocommentFloor(index)"
              v-if="item.showFloorComment.replyCount !== 0"
            >{{ item.showFloorComment.replyCount }}条回复></a>
          </div>
        </div>
      </div>
      <van-action-sheet
        v-model:show="drawer"
        :title="'回复'+ '('+floorTotal+')'"
        @closed="closeDrawer"
        @click-overlay="closeDrawer"
      >
        <div class="drawer">
          <div
            class="listItemDrawer"
            style="padding: 0 9px 0 0.3rem !important;border-bottom: 7px solid #f7f7f7;"
          >
            <el-image
              class="listItemImg"
              v-if="floorMaster.user"
              :src="floorMaster.user.avatarUrl"
              alt
              lazy
            />
            <div class="content">
              <div class="contentTop">
                <div class="left">
                  <span v-if="floorMaster.user">
                    {{
                    floorMaster.user.nickname
                    }}
                  </span>
                  <p>{{ conversionTime(floorMaster.time) }}</p>
                </div>
                <div class="right">
                  <span class="rightCount">{{ floorMaster.likedCount }}</span>
                  <span
                    :class="
                      floorMaster.liked
                        ? 'iconfont icon-shixin liked'
                        : 'iconfont icon-zan '
                    "
                  ></span>
                </div>
              </div>
              <div class="com">
                <p>{{ floorMaster.content }}</p>
              </div>
            </div>
          </div>
          <div class="listTitle">
            <span style="margin-top: 0.1rem;margin: 0.2rem 0 0.2rem 0.3rem;">全部回复</span>
          </div>
          <div class="listItemDrawer" v-for="(item, index) in floorCommentList" :key="index">
            <el-image class="listItemImg" :src="item.user.avatarUrl" alt lazy />
            <div class="content" style="margin:0 20px 0 0.2rem">
              <div class="contentTop">
                <div class="left">
                  <span>{{ item.user.nickname }}</span>
                  <p>{{ conversionTime(item.time) }}</p>
                </div>
                <div class="right">
                  <span class="rightCount">{{ item.likedCount }}</span>
                  <span
                    :class="
                      item.liked
                        ? 'iconfont icon-shixin liked'
                        : 'iconfont icon-zan '
                    "
                  ></span>
                </div>
              </div>
              <div class="com">
                <p>{{ item.content }}</p>
              </div>
            </div>
          </div>
        </div>
      </van-action-sheet>
    </div>
  </div>
</template>
<script>
import { getComments, getCommentFloor } from "@/tools/request"
import moment from "moment"

export default {
  props: ["isShowComment", "playDetail"],
  emits: ["showComment"],
  data () {
    return {
      // 普通评论
      commentList: [],
      // 评论数量
      comTotal: 0,
      // 当前歌曲信息
      nowPlayDetail: [],
      // 选中第几个评论类
      show: 0,
      // 显示吸顶bar
      showTop: false,
      // 显示楼层评论
      drawer: false,
      // 楼层回复个数
      floorTotal: 0,
      // 楼层评论列表
      floorCommentList: [],
      // 楼主信息
      floorMaster: [],
    }
  },
  mounted () {
    this.nowPlayDetail = this.playDetail
    this.getComCB()
    window.addEventListener("scroll", this.handleScrollx, true)
  },
  beforeUnmount () {
    window.removeEventListener("scroll", this.handleScrollx, true)
  },
  methods: {
    backComment () {
      this.$parent.$parent.showComment()
    },
    async getComCB (sortType = 1) {
      let result = await getComments(this.playDetail.id, sortType)
      if (result.data.code === 400) {
        this.show = 1
        let result2 = await getComments(this.playDetail.id, 2)
        this.commentList = result2.data.data.comments
        this.comTotal = result2.data.data.totalCount
        return
      }
      this.commentList = result.data.data.comments
      this.comTotal = result.data.data.totalCount
    },
    // 转换时间戳回调
    conversionTime (value) {
      return moment(value).format("MM月DD日")
    },
    oneClick () {
      this.show = 0
      this.getComCB(1)
    },
    towClick () {
      this.show = 1
      this.getComCB(2)
    },
    threeClick () {
      this.show = 2
      this.getComCB(3)
    },
    async gocommentFloor (index) {
      this.drawer = true
      let result = await getCommentFloor(
        this.commentList[index].commentId,
        this.nowPlayDetail.id
      )
      this.floorTotal = result.data.data.totalCount
      this.floorCommentList = result.data.data.comments
      this.floorMaster = result.data.data.ownerComment
    },
    // 监听滚动
    handleScrollx () {
      if (this.$refs.listTitle.getBoundingClientRect().top < 57) {
        this.showTop = true
      } else {
        this.showTop = false
      }
    },
    closeDrawer () {
      this.floorTotal = []
      this.floorCommentList = []
      this.floorMaster = []
    }
  },
}
</script>
<style lang="less" scoped>
.fixed {
  position: fixed;
  top: 1rem;
  left: 0;
  z-index: 3;
  width: 7.5rem;
  padding: 0.1rem 0.3rem;
  background-color: #fff;
}
.liked {
  color: #ea443b !important;
  font-size: 0.35rem !important;
}
// 隐藏滚动条
.comment::-webkit-scrollbar {
  width: 0 !important;
}
.comment {
  z-index: 24;
  position: fixed;
  top: 0;
  left: 0;
  width: 7.5rem;
  height: 100vh;
  background-color: #fff;
  overflow-y: scroll;
  overflow-x: hidden;
  .playTop {
    position: fixed;
    top: 0;
    left: 0;
    display: flex;
    width: 7.5rem;
    height: 1rem;
    padding: 0.3rem;
    align-items: center;
    justify-content: space-between;
    color: #333;
    font-weight: 600;
    z-index: 2;
    background-color: #fff;
    .iconfont {
      font-size: 0.4rem;
    }
    .icon-fenxiang {
      font-size: 0.45rem;
    }
  }
  .playController {
    position: relative;
    width: 7.5rem;
    height: 0.9rem;
    display: flex;
    background-color: #fff;
    transition: all 0.5s;
    margin-top: 1rem;
    margin-bottom: 0.1rem;
    .img {
      position: absolute;
      left: 0.4rem;
      width: 0.8rem;
      height: 0.8rem;
      background-color: #000;
      display: flex;
      justify-content: center;
      align-items: center;
      border-radius: 50%;
      img {
        width: 0.55rem;
        height: 0.55rem;
        border-radius: 50%;
      }
    }
    .left {
      flex: 6;
      display: flex;
      flex-direction: column;
      justify-content: center;
      margin-left: 1.3rem;
      p {
        font-size: 0.25rem;
        color: #333;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
        max-width: 6rem;
      }
      span {
        font-size: 0.1rem;
        color: #777;
        white-space: nowrap;
        overflow: hidden;
        text-overflow: ellipsis;
      }
    }
  }
  .listTitle {
    display: flex;
    justify-content: space-between;
    align-items: center;
    font-size: 0.26rem;
    font-weight: 700;
    color: #333;
    .tabBarList {
      font-size: 0.25rem;
      color: #999;
      font-weight: 200;
      .active {
        font-weight: 700;
        color: #333;
      }
      .line {
        margin: 0 0.1rem;
      }
    }
  }
  .commentList {
    width: 7.5rem;
    padding: 0.3rem 0.3rem;
    border-top: 7px solid #f7f7f7;
    .listItemDrawer {
      padding: 0.2rem 0 0 0.3rem !important;
    }
    .listItemDrawer,
    .listItem {
      width: 7.5rem;
      display: flex;
      padding: 0.3rem 0.35rem 0 0;
      .listItemImg {
        width: 0.7rem;
        height: 0.7rem;
        border-radius: 50%;
      }
      .content {
        flex: 1;
        display: flex;
        flex-direction: column;
        margin: 0 0.2rem;
        .contentTop {
          display: flex;
          justify-content: space-between;
          align-items: center;
          .left {
            position: relative;
            display: flex;
            flex-direction: column;
            align-items: flex-start;
            p {
              font-size: xx-small;
              transform: scale(0.9) translate(-5px, 0);
              color: #999;
            }
            span {
              font-size: 0.25rem;
              color: #666;
            }
          }
          .right {
            display: flex;
            align-items: center;
            span {
              font-size: 0.28rem;
              margin-right: 0.12rem;
              color: #777;
            }
            .rightCount {
              display: block;
              font-size: 0.2rem !important;
              transform: scale(0.9);
            }
          }
        }
        .com {
          padding: 0.1rem 0 0.2rem 0;
          border-bottom: 1px solid #eee;
          p {
            font-size: 0.27rem;
            letter-spacing: 0.5px;
            line-height: 1.7;
            color: #444;
            max-width: 6rem;
          }
          a {
            color: #6281bb;
            font-size: 0.2rem;
            margin-top: 0.1rem;
            display: block;
          }
        }
      }
    }
  }
  .drawer {
    .drawerHeader {
      position: fixed;
      top: 0.92rem;
      left: 0;
      width: 7.5rem;
      background-color: #fff;
      padding: 0.2rem;
      border-radius: 20px 20px 0 0;
      z-index: 3;
      transition: all 0.5s;
      p {
        display: flex;
        justify-content: center;
        font-weight: bold;
        color: #444;
      }
      .iconfont {
        position: absolute;
        top: 12px;
        right: 20px;
        transform: rotate(45deg);
        -webkit-transform: rotate(45deg);
        transition: all 0.5s;
      }
      .iconfont:active {
        transform: scale(0.8);
      }
    }
  }
}
</style>
